@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

:root {
  --primary-color: #0DFF92;
  --dark-color: #222222;
  --light-color: #f0f0f0;
}

body,
html {
  height: 100%;
  background: var(--dark-color);
  font-family: 'Poppins', sans-serif;
  font-size: 16px; /* Global font size */
  color: var(--light-color); /* Global text color */
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #fff;
  background-color: var(--primary-color);
}

.container {
  display: block;
  position: relative;
  margin: 40px auto;
  height: auto;
  width: 800px;
  padding: 20px;
}

.container .title {
  font-size: 3em;
  text-align: center;
  border: 5px solid var(--primary-color);
  padding: 0.3em 0.2em;
  border-radius: 4px;
}

.text-align {
  color: var(--light-color);
}

.container .btn {
  padding: 0.2em 1.7em;
  border: none;
  border-radius: 0.1em;
  font-size: 1.2em;
  cursor: pointer;
}

.container .btn:hover {
  background-color: #f0f0f0;
  color: #202020;
}

.next {
  background-color: var(--primary-color);
  justify-self: flex-end;
}

.prev {
  background-color: #faff5a;
  justify-self: flex-start;
}

ul li:hover label {
  color: #FFFFFF;
}

ul li .check {
  display: block;
  position: absolute;
  border: 5px solid #AAAAAA;
  border-radius: 100%;
  height: 25px;
  width: 25px;
  top: 30px;
  left: 20px;
  z-index: 5;
}

/* Main Page Styles */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333; /* Or a darker variation of var(--dark-color) */
}

.welcome-message {
  font-size: 24px;
  font-weight: bold;
}

.start-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

/* Result Page Styles */
.score-display {
  font-size: 36px;
  font-weight: bold;
  color: var(--primary-color);
}

.result-message {
  font-size: 18px;
  margin-top: 20px;
}

/* Router Transitions (Optional) */
.router-enter-active,
.router-leave-active {
  transition: opacity 0.3s ease;
}

.router-enter,
.router-leave-to {
  opacity: 0;
}
