/* Add this to your existing styles or create a new CSS file */

/* Responsive styles */
.container {
  max-width: 100%;
  overflow-x: hidden;
}

.result {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 10px;
}

.bold {
  font-weight: bold;
}

.start {
  display: flex;
  justify-content: center;
  padding-top: 2em;
}

.btn {
  padding: 0.2em 1.7em;
  border: none;
  border-radius: 0.1em;
  font-size: 1.2em;
  color: #202020;
  text-decoration: none;
  background-color: #faff5a;
  cursor: pointer;
}

/* Media query for smaller screens */
@media screen and (max-width: 768px) {
  .result {
    text-align: center;
  }

  .flex {
    flex-direction: column;
    align-items: center;
  }

  .bold {
    margin-top: 10px; /* Adjust spacing for smaller screens */
  }

  .start {
    padding-top: 1em;
  }
}
