  /* Base Styles */
.container ol li {
    font-size: 1.4em;
    color: #cecece;
}

.start {
    display: flex;
    justify-content: center;
    padding-top: 2em;
}

.start .btn {
    padding: 0.2em 1.7em;
    border: none;
    border-radius: 0.1em;
    font-size: 1.2em;
    color: #202020;
    text-decoration: none;
    background-color: #faff5a;
}

#form {
    display: flex;
}

/* Responsive Styles */
@media only screen and (max-width: 768px) {
    .container {
        width: 90%;
    }

    .start {
        flex-direction: column;
        align-items: center;
    }

    .start .btn {
        margin-top: 1em;
    }
}

@media only screen and (max-width: 480px) {
    .container ol li {
        font-size: 1.2em;
    }

    .start .btn {
        font-size: 1em;
        padding: 0.2em 1.2em;
    }
}
